<template>
	<view class="body">
		<view class="content">
			<!-- 下拉选择框 -->
			<!-- <view class="options">
				<view class="title">已经购买商品</view>
				<uni-data-select
				    v-model="value"
				    :localdata="range"
				    @change="changeCheck"
				></uni-data-select>
			</view> -->
			<view class="comment">
				<view class="title">填写评价</view>
				<view class="box">
					<textarea name="comment" cols="30" rows="8" v-model="comment" maxlength='500' @input="length"></textarea>
					<view v-if="file" class="imgs">
						<img mode="aspectFit" :src="file" @click="preview">
					</view>
					<view class="commentInfo">
						<view class="length">{{commentLength}}/500</view>
						<view class="picture">
							<img @click="choosePhotos" src="../../static/mine/camera.png">
							<img @click="choosePhoto" src="../../static/mine/picture.png">
						</view>
					</view>
				</view>
			</view>
			<button class="submit" @click="submit()" >提交</button>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		evaluate,uploadFile
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				value: 0,
				// 选择框下拉数组
				/* range: [
					{ value: 0, text: "313历史学综合" },
					{ value: 1, text: "思维导图(完整版)" }
				], */
				comment:'',
				id:'',
				token:'',
				commentLength:0,
				file:[]
			}
		},
		onLoad(option) {
			this.id = option.id
			this.token = store.state.vuex_token
		},
		methods: {
			changeCheck(e) {
				console.log("e:", e);
			},
			length(){
				this.commentLength = this.comment.length
			},
			// 预览图片
			preview() {
				let file = []
				file.push(this.file)
				uni.previewImage({
					urls: file
				});
			},
			// 相机选择图片
			choosePhotos(){
				uni.chooseImage({
					count: 1, 
					sourceType: ['camera'], 
					sizeType: ['original'],
					success:(res)=>{
						console.log('选择图片完成', res)
						console.log(res.tempFilePaths[0])
						// 调用上传图片的接口
						this.file = res.tempFilePaths[0]
					},
					fail(err) {
						console.log('失败', err);
					},
					complete() {
						console.log('结束');
					},
				})
			},
			// 相册选择图片
			choosePhoto(){
				uni.chooseImage({
					count: 1, 
					sourceType: ['album'], 
					sizeType: ['original'],
					success:(res)=>{
						console.log('选择图片完成', res)
						console.log(res.tempFilePaths[0])
						// 调用上传图片的接口
						this.file = res.tempFilePaths[0]
					},
					fail(err) {
						console.log('失败', err);
					},
					complete() {
						console.log('结束');
					},
				})
			},
			submit() {
				if(this.file.length!=0){
					uni.uploadFile({
						url:'https://api.ydwechat.com/apiindex.php/xcx.subscribe/uploadFile',
						header:{
							// HTTP 请求 Header, header 中不能设置 Referer
						},
						filePath: this.file, // 要上传文件资源的路径
						name: 'file', // 文件对应的 key , 开发者在服务器端通过这个 key 可以获取到文件二进制内容
						formData:{
							
						},
						success:(res)=> {
							console.log(res)
							let ress = JSON.parse(res.data);
							console.log('上传成功', ress);
							evaluate({token:this.token,order_id:this.id,content:this.comment,img:ress.data}).then(res=>{
								console.log(res)
								if(res.data.data=='评论成功'){
									this.$u.toast(res.data.data+"即将返回")
									setTimeout(()=>{
										uni.navigateBack({
											delta:1
										})
									},1000)
								}else{
									this.$u.toast(res.data.data)
								}
							}).catch(err=>{
								console.log(err)
							})
						},
						fail() {
							console.log('失败', err);
						},
						complete() {
							console.log('结束');
						},
					})
				}else{
					evaluate({token:this.token,order_id:this.id,content:this.comment}).then(res=>{
						console.log(res)
						if(res.data.data=='评论成功'){
							this.$u.toast(res.data.data+"即将返回")
							setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},1000)
						}else{
							this.$u.toast(res.data.data)
						}
					}).catch(err=>{
						console.log(err)
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	.body{
		min-height: 100vh;
		padding-top: 1rpx;
		background-color: #f7f7fb;
		.content{
			width: 90%;
			margin: 30rpx 5%;
			.options{
				background-color: #fff;
				border-radius: 20rpx;
				padding: 30rpx;
				margin-bottom: 30rpx;
				.title{
					font-size: 34rpx;
					font-weight: 600;
					margin-bottom: 20rpx;
				}
				.uni-select__input-text{
					font-size: 28rpx;
					line-height: 80rpx !important;
					background-image: url(../../static/mine/comment_cell_icon.png);
					background-repeat: no-repeat;
					background-position: 96% center;
					background-size: 20rpx 14rpx;
				}
				.cell{
					font-size: 28rpx;
					border: 1rpx solid #e1e1e1;
					line-height: 80rpx;
					padding: 0 20rpx;
					background-image: url(../../static/mine/comment_cell_icon.png);
					background-repeat: no-repeat;
					background-position: 96% center;
					background-size: 16rpx 10rpx;
				}
			}
			.comment{
				background-color: #fff;
				border-radius: 20rpx;
				padding: 30rpx;
				margin-bottom: 30rpx;
				position: relative;
				.title{
					font-size: 34rpx;
					font-weight: 600;
					margin-bottom: 20rpx;
				}
				.box{
					width: calc(100% - 30px);
					padding: 30rpx;
					border: 1rpx solid #e5e5e5;
					border-radius: 8rpx;
					.imgs{
						margin-top: 20rpx;
						img{
							width: 200rpx;
							height: 200rpx;
						}
					}
					.commentInfo{
						width: calc(100% - 45px);
						position: absolute;
						left: 60rpx;
						bottom: 50rpx;
						display: flex;
						justify-content: space-between;
						align-items: center;
						background-color: #fff;
						z-index: 2;
						.length{
							font-size: 22rpx;
							color: #666;
						}
						.picture{
							img{
								width: 50rpx;
								height: 40rpx;
								margin-right: 50rpx;
							}
						}
					}
				}
			}
			.submit{
				width: 90%;
				margin: 30rpx 5%;
				background-color: #6ab3ff;
				color: #fff;
				font-size: 30rpx;
				border-radius: 50rpx;
				line-height: 78rpx;
			}
		}
	}
</style>